.chat-wrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: linear-gradient(var(--green) 0%, var(--green) 130px, var(--paleGray) 130px, var(--paleGray) 100%);
}
.chat-container{
    position: relative;
    width: 1396px;
    max-width: 100%;
    height: calc(100vh - 40px);
    background: var(--white);
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.06), 0 2px 5px 0 rgba(0, 0, 0, 0.06);
    display: flex;
}
.left-side{
    position: relative;
    flex: 30%;
    background: var(--white);
    border-right: 1px solid rgba(0, 0, 0, 0.2);
}
.right-side{
    position: relative;
    flex: 70%;
    background: #e5ddd5;
}
.right-side::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* background: url(contact-png.png); */
    opacity: 0.06;
}
.header{
    position: relative;
    width: 100%;
    height: 60px;
    background: #ededed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
}
.chat-user{
    display: flex;
    align-items: center;
}
.userimg{
    position: relative;
    width: 40px;
    height: 40px;
    overflow: hidden;
    border-radius: 50%;
    cursor: pointer;
    margin-right: 20px;
}
.chat-cover{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.nav-icons{
    display: flex;
}
.nav-icons > li{
    display: flex;
    list-style: none;
    cursor: pointer;
    color: #51585c;
    font-size: 1.5em;
    margin-left: 22px;
}
.search-chat{
    position: relative;
    width: 100%;
    height: 50px;
    background: #f6f6f6;
    display: flex;
    justify-content: center;
    align-items: center;
}
.search-chat > div{
    width: 100%;
}
.search-chat > div > input{
    width: 100%;
    outline: none;
    border: none;
    background: var(--white);
    padding: 6px;
    height: 38px;
    border-radius: 30px;
    font-size: 14px;
    padding-left: 40px;
}
.search-chat > div > input::placeholder{
    color: #bbb;
}
.chat-search{
    position: absolute;
    left: 15px;
    top: 14px;
    font-size: 1.2em;
}
.chatlist{
    position: relative;
    height: calc(100% - 110px);
    overflow-y: auto;
}
.chatlist > h5{
    margin-top: 20px;
    margin-bottom: 15px;
    /* margin: 20px 0; */
}
.block{
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 15px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.06);
    cursor: pointer;
}
.block:hover{
    background: #f5f5f5;
}
.chat-selected{
    background: #f5f5f5;
}
.active{
    background: #ebebeb;
}
.imgbx{
    position: relative;
    min-width: 45px;
    height: 45px;
    overflow: hidden;
    border-radius: 50%;
    margin-right: 10px;
}
.details{
    position: relative;
    width: 100%;
}
.listhead{
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}
.listhead > h4{
    font-size: 1.1em;
    font-weight: 600;
    color: #111;
}
.block > .details > .listhead > .time{
    font-size: 0.75em;
    color: #aaa;
    color: #111;
}
.unread > .details > .listhead > .time{
    color: #06d755;
}
.message-p{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.message-p > p{
    color: #aaa;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    font-size: 0.9em;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
.message-p > b{
    background: #06d755;
    color: var(--white);
    min-width: 20px;
    height: 20px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    font-size: 0.75em;
    padding: 4px;
}
/* right side */
.imgText{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}
.imgText > h4{
    font-weight: bold;
    line-height: 1.2em;
    margin-left: 15px;
}
.imgText > h4 > span{
    font-size: 0.8em;
    color: #555;
}
/* chatbox */
.chatbox{
    position: relative;
    width: 100%;
    height: calc(100% - 120px);
    padding: 50px;
    overflow-y: auto;
}
.message{
    position: relative;
    display: flex;
    width: 100%;
    margin: 5px 0;
}
.message > p{
    position: relative;
    right: 0;
    text-align: right;
    max-width: 65%;
    padding: 12px;
    background: #dcf8c6;
    border-radius: 10px;
    font-size: 0.9em;
}
.message > p::before{
    content: '';
    position: absolute;
    top: 0;
    right: -12px;
    width: 20px;
    height: 20px;
    background: linear-gradient(135deg, #dcf8c6 0%, #dcf8c6 50%, transparent 50%, transparent);
}
.message > p > span{
    display: block;
    margin-top: 5px;
    font-size: 0.85em;
    opacity: 0.5;
}
.my-message{
    justify-content: flex-end;
}
.fri-message{
    justify-content: flex-start;
}
.fri-message > p{
    background: var(--white);
    text-align: left;
}
.fri-message > p::before{
    content: '';
    position: absolute;
    top: 0;
    left: -12px;
    width: 20px;
    height: 20px;
    background: linear-gradient(225deg, var(--white) 0%, var(--white) 50%, transparent 50%, transparent);
}